.s-error {
    position: relative;
    padding: rem(25) 0;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;

    @media (min-width: 768px) {
        padding: rem(35) 0;
    }

    @media (min-width: 1024px) {
        padding: rem(45) 0;
    }

    .l-default {
        position: static;
    }

    &__note {
        max-width: rem(255);
        margin-bottom: rem(240);
        font-size: rem(17);
        line-height: 1.3;
        color: $grey_light;

        @media (min-width: 768px) {
            max-width: rem(656);
            margin-bottom: rem(338);
            font-size: rem(23);
        }

        @media (min-width: 1024px) {
            max-width: rem(715);
            margin-bottom: rem(250);
            font-size: rem(25);
        }

        @media (min-width: 1600px) {
            max-width: rem(784);
            margin-bottom: rem(115);
            font-size: rem(27);
        }

        @media (min-width: 1920px) {
            margin-bottom: rem(121);
            font-size: rem(30);
        }

        br {
            display: none;

            @media (min-width: 768px) {
                display: block;
            }
        }

        span {
            color: $white;
        }
    }

    &__title {
        position: absolute;
        top: 0;
        right: 0;
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 600;
        font-size: 550px;
        line-height: 1;
        letter-spacing: -0.03em;
        color: #000203;
        mix-blend-mode: normal;
        opacity: 0.3;

        @media (max-width: 1699px) {
            font-size: 490px;
        }

        @media (max-width: 1199px) {
            font-size: 450px;
        }

        @media (max-width: 991px) {
            top: 60px;
            font-size: 400px;
        }

        @media (max-width: 600px) {
            top: 124px;
            font-size: 250px;
        }

        @media (max-width: 374px) {
            font-size: 200px;
        }
    }

    &__subtitle {
        margin-bottom: rem(18);
        font-size: rem(50);
        font-weight: 600;
        line-height: 1;
        color: $grey;

        @media (min-width: 768px) {
            font-size: rem(90);
        }

        @media (min-width: 1920px) {
            margin-bottom: rem(25);
            font-size: rem(100);
        }

        svg {
            width: rem(15);
            margin-left: 4px;
            margin-bottom: -4px;
        }
    }

    &__text {
        margin-bottom: rem(42);
        font-size: rem(19);
        line-height: 1.3;
        color: $white;

        @media (min-width: 768px) {
            max-width: rem(550);
            margin-bottom: rem(70);
            font-size: rem(23);
        }

        @media (min-width: 1024px) {
            max-width: rem(625);
        }

        @media (min-width: 1600px) {
            margin-bottom: rem(140);
            font-size: rem(25);
        }

        @media (min-width: 1920px) {
            max-width: rem(700);
            margin-bottom: rem(150);
        }

        br {
            display: none;

            @media (min-width: 1200px) {
                display: block;
            }
        }
    }

    &__buttons {
        display: grid;
        margin-bottom: rem(60);
        grid-template-columns: repeat(1, 1fr);
        gap: rem(34);

        @media (min-width: 768px) {
            margin-bottom: rem(90);
            gap: rem(34) rem(30);
            grid-template-columns: repeat(2, 1fr);
        }

        @media (min-width: 1024px) {
            margin-bottom: rem(115);
            gap: rem(34) rem(150);
            grid-template-columns: repeat(2, 1fr);
        }

        @media (min-width: 1600px) {
            margin-bottom: rem(120);
            gap: rem(40);
            grid-template-columns: repeat(4, 1fr);
        }

        @media (min-width: 1920px) {
            gap: rem(120);
        }
    }

    &__item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        gap: rem(15) 0;

        @media (min-width: 768px) {
            gap: rem(15) 0;
        }

        @media (min-width: 1024px) {
            gap: rem(15) 0;
        }

        &-text {
            font-size: rem(15);
            line-height: 1.1;
            color: $grey;
            flex: none;
            letter-spacing: -0.03em;
        }

        &-button {
            display: flex;
            align-items: center;
            justify-content: center;
            width: rem(167);
            height: rem(38);
            max-width: 100%;
            flex: none;
        }
    }
}
